<template>
    <div class="box">  
         <div v-show="bool">暂无关注</div>
           <ul>
            <li v-for="(ietm,index) in history" :key="index">
                 <transition  name="lianimate">
                 <div v-show="ietm.gz"  class="follow-style"> 
                    <img :src="ietm.header" alt="">
                    <span>{{ietm.name}}</span>
                    <span class="remove-follow" @click="removeFollow(ietm)">取消关注</span>
                </div>
                </transition>
            </li>
            </ul>
</div>
</template>
<script>
export default {
    props:[
        'history'
    ],
    data(){
        return{
           bool:true
        }
    },
    created(){
        for(let i = 0 ; i <this.history.length;i++){
            if(this.history[i].gz===true){
                 this.bool = false
            }
        }
    },
    methods:{
        removeFollow(ietm){
            ietm.gz=false
        }
    }
}
</script>
<style lang="scss" scoped>
.box{
    margin-top: 150px;

    .lianimate-leave-active {
        transition: all .5s linear;
    }
    .lianimate-leave-to {
        transform: translateX(-100px);
        opacity: 0;
    }

    .follow-style{
        margin: 10px 0;
        padding: 0 5% 5px 5%;
        width: 100%;
        height: 35px;
        border-bottom: 1px solid #ddd;

        img{
            width: 30px;
            height: 30px;
            border-radius: 50%;
        }

        span{
            display: inline-block;
            margin: 0 10px;
            position: relative;
            top: -7.5px;
            line-height: 30px;
            height: 30px;
            font-size: 14px;
            
        }

        .remove-follow{
            float: right;
            margin-top: 7.5px;
            padding: 0px 5px;
            border: 1px solid  #de1a00;
            border-radius: 5px;
            color: #de1a00;
            
        }
    }
}
</style>